.logo {
  &-font,
  &-graph {
    position: relative;
    width: 100%;
    display: flex;
  }

  &-font {
    &--horizon {
      height: auto;
    }
  }

  &-graph {
    height: 100%;

    &--main-complex {

    }
  }

  &-image {
    width: 100%;
    height: 100%;
  }

  &-shadowed {
    position: absolute;
    width: 100%;
    z-index: 0;
    opacity: 1;
  }
}
